<template>
    <div>
        <v-btn
            class="mx-2 right_20 top_85 mr-0 z-L"
            fab
            
            :color="color"
            :class="extra_calss"

            :fixed="is_fixed"

            :left="l_or_r"
            :right="!l_or_r"
            :x-small="is_small"

            :disabled="useless"

            @click="lockPrint"
        >
            <v-icon v-if="!loading">
                {{ icon }}
            </v-icon>
            <v-icon v-else class="circle-around">mdi-loading</v-icon>
        </v-btn>
        <div class="pdf-fixed-iframe" id="_data">
            <!-- pdf-user></pdf-user-->
            <iframe :src="link">
            </iframe>
        </div>

    </div>
</template>

<script>
import PdfUser from '../PdfUser/PdfUser.vue'
    export default {
  components: { PdfUser },
        name: '',
        props: {

            icon: {
                type: String,
                default: 'mdi-file-pdf-box-outline'
            },
            color: {
                type: String,
                default: ''
            },
            extra_calss: {
                type: String,
                default: ''
            },
            is_fixed: {
                type: Boolean,
                default: true
            },
            l_or_r: {
                type: Boolean,
                default: false
            },
            is_small: {
                type: Boolean,
                default: false
            },

            useless: {
                type: Boolean
            }
        },
        data() {
            return {
                print: false,

                link: 'https://fanyi.youdao.com/',

                loading: false
            }
        },
        methods: {
            lockPrint() {
                this.loading = true
                this.link = 'https://fanyi.youdao.com/'
                const uri = this.conf.baseURL + '/#/home/dump/?menu=false&unid=' + this.$store.state.user_backend.unid + '&chronu_id=' + this.$store.state.chronus.id
                // const uri = 'http://127.0.0.1:8080//#/home/dump/?menu=false&unid=' + this.$store.state.user_backend.unid + '&chronu_id=' + this.$store.state.chronus.id
                this.link = uri
                console.log('URI =', uri)

                setTimeout(() => {
                    this.loading = false
                }, 3400)
            }
        },
    }
</script>

<style lang="sass" scoped>
.pdf-fixed-iframe
    position: fixed
    top: 20vh
    left: 0
    z-index: 999
    width: 100vw
    height: 30vh !important
    overflow: scroll
    // visibility: hidden

.pdf-fixed-iframe iframe
    width: 100%

.circle-around
    animation: around 1s cubic-bezier(0, 0.2,0.2,0) infinite

@keyframes around
    0%
        transform: rotate(0deg)
        -webkit-transform: rotate(0deg)
    50%
        transform: rotate(180deg)
        -webkit-transform: rotate(180deg)
    100%
        transform: rotate(360deg)
        -webkit-transform: rotate(360deg)
</style>


