            <div class="qiong-wide-50 pr-0">
                iPV：{{ view.if_def(_bonus.ipv) }}
                <!-- 支付級別：<level-viewing :item="_bonus.vip" class="d-inline"></level-viewing -->
            </div>
        </div>
        <div class="">
            用戶全稱：{{ view.if_def(_bonus.user.display_name) }}
        </div>
        <!-- div class="qiong-menu-sub">
            
        </div -->
    </div>

</template>

<script>
import QiongLoading from '../../Qiong/Ui/QiongLoading.vue'
import LevelViewing from '../../Viewing/LevelViewing.vue'
    export default {
        props: {
            _bonus: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        components: { 
            LevelViewing, QiongLoading          
        },
    }
</script>

<style lang="sass" scoped>
</style>


