<template>

    <v-list nav dense class="px-0">
        <lad-avatar
            :_avatar="face"
            :_need_avatar="need_avatar"
            :_bonus="bonus"
        ></lad-avatar>

            <lad-msg class="qiong-pt-5 mt-1 pl-4" 
                :_bonus="bonus"
                v-if="bonus"
            ></lad-msg>
            <div v-else class="Lad_Three_Line pt-9">
                <qiong-loading :bigger="-1"></qiong-loading>
            </div>

        <qiong-gradient-line class="qiong-pt-5 mt-1"></qiong-gradient-line>

            <lad-bonus class="qiong-pt-10 pl-4"
                :_bonus="bonus"
                v-if="bonus"
            ></lad-bonus>
            <div v-else class="Lad_Two_Line pt-5">
                <qiong-loading :bigger="-1"></qiong-loading>
            </div>

    </v-list>

</template>

<script>
import LadBonus from './Inner/LadBonus.vue'
import LadMsg from './Inner/LadMsg.vue'
import QiongGradientLine from '../Qiong/Ui/QiongGradientLine.vue'
import QiongLoading from '../Qiong/Ui/QiongLoading.vue'
import LadAvatar from './Inner/LadAvatar.vue'

    export default {
  components: { LadBonus, LadMsg, QiongGradientLine, LadAvatar, QiongLoading },
        name: '',
        props: {
            id: {
                type: String,
                default: ''
            },
            need_avatar: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                face: require('../../assets/FACE.jpg'),
                unid: null,

                bonus: null,
                loading: false
            }
        },
        mounted() {
            // 赋值函数
            this._assign()
            
            // 获取数据
            this.bonusLoading()
        },
        methods: {
            _assign(_id = '') {
                if (this.id) { 
                    this.unid = this.id
                } else {
                    this.unid = _id
                }
            },

            async bonusLoading() {

                const res = await this.conn.get(
                    this.api.bonus,
                    this.$store.state.token,
                    {
                        'chronu': this.$store.state.chronus.id,
                        'user.unid': this.unid
                    }
                )

                if (res) {
                    this.bonus = res[0]
                }

                this.loading = true
            }
        },
    }
</script>

<style lang="sass" scoped>
    
.Lad_Two_Line
    height: 45px

.Lad_Three_Line
    min-height: 80px

</style>


