<template>
    <div class="d-inline">
        <div v-if="bigger == -1" class="vip-img-S" :class="extra_class">
            <img :src="result">
        </div>
        <div v-if="bigger == 2" class="vip-img-XXL" :class="extra_class">
            <img :src="result">
        </div>
    </div>
</template>

<script>
import api_bonus from '@/common/es/api_bonus/index'
import LoadingSmall from '../Data/LoadingSmall.vue'

    export default {
        components: { LoadingSmall },
        props: {
            item: {
                type: String
            },
            def: {
                type: String,
                default: 'ICONS/vip_simpler_1.png'
            },
            bigger: {
                type: Number,
                default: -1
            },

            extra_class: {
                type: String
            }
        },
        name: '',
        data() {
            return {
                addr: null
            }
        },
        mounted() {
            
        },
        computed: {
            result() {
                let addr = '/static/'

                if (this.item != undefined && this.item != 'undefined_0') { 
                    let res = api_bonus.vip_view.split_code(this.item)
                    res = api_bonus.vip_view.code_to_content( res )

                    if (res) { 
                        addr += res.img 
                    } else { 
                        addr += this.def 
                    }
                }

                console.log('图片地址 =', addr)
                return addr
            },
        },
    }
</script>

<style lang="sass" scoped>
.vip-img-S,
.vip-img-XXL,
.vip-img-S img,
.vip-img-XXL img
    display: inline
    width: 15px !important
    height: auto

.vip-img-XXL ,
.vip-img-XXL img
    width: calc( 60px + 2vw ) !important
</style>


