<template>

    <v-list-item-group>
        <v-list-item class="pl-8">
            <v-list-item-avatar size="69" v-if="_need_avatar">
                <v-img :src="_avatar"></v-img>
            </v-list-item-avatar>
            <v-list-item-content v-if="_bonus">
                <v-list-item-title class="fs-18">
                    
                    {{ _bonus.user.username }}
                </v-list-item-title>
                <v-list-item-subtitle class="">
                    
                    <level-image-viewing :item="_bonus.vip"></level-image-viewing>
                    <level-viewing :item="_bonus.vip" class="fs-12 d-inline op-80"></level-viewing>
                </v-list-item-subtitle>
            </v-list-item-content>

            <v-list-item-content v-else>
                <v-list-item-title class="fs-18">
                    
                    匿名用户
                </v-list-item-title>
                <v-list-item-subtitle>
                    
                    --
                </v-list-item-subtitle>
            </v-list-item-content>

        </v-list-item>
    </v-list-item-group>

</template>

<script>
import LevelViewing from '../../Viewing/LevelViewing.vue'
import LevelImageViewing from '../../Viewing/LevelImageViewing.vue'
    export default {
        name: '',
        components: {
            LevelImageViewing,
                LevelViewing
        },
        props: {
            _need_avatar: {
                type: Boolean
            },
            _avatar: {
                type: String
            },
            _bonus: {
                type: Object
            }
        },
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.def-named
    min-height: 30px
</style>


