<template>

    <v-list-item-group class="qiong-br-0 qiong-list-item"
        v-model="group" active-class="deep-blue--text text--accent-5"
    >
        <v-list-item
            v-for="tile in tiles"
            :key="tile.title"

            @click.stop="go(`/home/${tile.link}`)"
        >
            <v-list-item-avatar class="py-0 my-0">
                <v-icon v-html="tile.icon"></v-icon>
            </v-list-item-avatar>

            <v-list-item-content>
                <div tag="div">
                    {{ tile.title }}
                </div>
            </v-list-item-content>
        </v-list-item>


        <!-- 樹形結構 -->
        <v-list-group
            v-for="tile in tiles_group"
            :key="tile.title"

            no-action
            sub-group
            color="rgba(255, 255, 255, 0.6)"
            :prepend-icon="tile.icon"
            class="lad_list_group"
        >
            <template v-slot:activator>
                <v-list-item-content>
                    <v-list-item-title class="pl-2 fs-16">{{ tile.title }}</v-list-item-title>
                </v-list-item-content>
            </template>
            <v-list-item
                class="pl-15"
                color="white"

                v-for="t in tile.children"
                :key="t.title"

                @click.stop="go(`/home/${t.link}`)"
            >
                <v-list-item-avatar class="pr-0 mr-3">
                    <v-icon :size="22">{{ t.icon }}</v-icon>
                </v-list-item-avatar>

                <v-list-item-content>
                    <div tag="div">
                        {{ t.title }}
                    </div>
                </v-list-item-content>
            </v-list-item>
        </v-list-group>

        <!-- 編輯月份 -->
        <v-list-item
            @click="editChronu"
        >
            <v-list-item-avatar>
                <v-icon>mdi-application-cog</v-icon>
            </v-list-item-avatar>

            <v-list-item-content>
                <div tag="div">
                    編輯月份
                </div>
            </v-list-item-content>
        </v-list-item>

        <!-- 退出登錄
        <v-list-item
            @click="outLogin"
        >
            <v-list-item-avatar>
                <v-icon>mdi-logout-variant</v-icon>
            </v-list-item-avatar>

            <v-list-item-content>
                <div tag="div">
                    退出登錄
                </div>
            </v-list-item-content>
        </v-list-item -->

    </v-list-item-group>
</template>

<script>
    export default {
        name: '',
        data() {
            return {
                group: null,
                tiles: [
                    { icon: 'mdi-account-tie-outline', title: '個人中心', link: 'user/center' },
                    { icon: 'mdi-clipboard-multiple-outline', title: '購買記錄', link: 'buys/records' },
                    { icon: 'mdi-certificate', title: '獎金報表', link: 'account/statement' },
                ],

                tiles_group: [

                    { icon: 'mdi-trophy-variant-outline', title: '獎勵明細', link: null,
                        children: [
                            { icon: 'mdi-bell-ring-outline', title: '活躍分店獎勵', link: 'bonus/active_house' },
                            { icon: 'mdi-basket-outline', title: '首購獎勵', link: 'bonus/recommend' },
                            { icon: 'mdi-account-group-outline', title: '團隊獎金', link: 'bonus/active_layer' },
                        ]
                    },

                    { icon: 'mdi-family-tree', title: '樹形結構', link: null,
                        children: [
                            { icon: 'mdi-file-tree-outline', title: 'Sponser 結構', link: 'user/sponser' },
                            { icon: 'mdi-file-tree', title: 'Enroller 結構', link: 'user/enroller' }
                        ]
                    },
                ]
            }
        },
        methods: {
            go(path) {
                this.$router.push({
                    path
                })
            },

            outLogin() {
                this.$store.commit('changeAuth', null)
                this.$store.commit('saveBackendUser', '')

                this.$router.push({ path: '/login/' })
            },

            editChronu() {
                this.$store.commit('saveChronus', [ '', true ])
            }
        },
    }
</script>

<style lang="sass" scoped>
.lad_list_group
    margin-left: -8px !important
.lad_list_group > div
    padding-left: 16px !important

.qiong-list-item div
    border-radius: 0px !important
.qiong-list-item:hover div
    border-radius: 0px !important

</style>


