<template>
    <home-layout v-if="$store.state.user_backend">

        <div slot="menu" v-if="menu">
            <left-app-drawers class="menu" ref="menu"></left-app-drawers>
        </div>

        <div slot="content">
            <transition :name="transitionName" v-if="alive">

                <router-view v-if="init_finished"/>

                <data-running v-else></data-running>
            </transition>
            
            <init ref="initREF" v-if="$store.state.chronus"></init>

        </div>

    </home-layout>
    <qiong-loading v-else></qiong-loading>
</template>

<script>
import HomeLayout from '@/components/Layout/HomeLayout.vue'
import Chronus from './Chronus/Chronus.vue'
import Init from '../../components/Data/Init.vue'
import QiongLoading from '../../components/Qiong/Ui/QiongLoading.vue'
import DataRunning from './DataRunning/DataRunning.vue'
import LeftAppDrawers from '../../components/Bar/LeftAppDrawers.vue'
import FlexIframePdf from '../i/Pdf/FlexIframePdf.vue'

    export default {
        components: { HomeLayout, Chronus, Init, QiongLoading, DataRunning, LeftAppDrawers, FlexIframePdf },
        name: '',
        created() {
            const _menu = this.$route.query.menu
            if (_menu) {
                this.menu = JSON.parse(_menu)
            } else {
                this.menu = true
            }
        },
        provide() {
            return {
                reload: this.reload
            }
        },
        watch: {
            '$route' (to, from) {

                const _to = to.path.split('/').length
                const _from = from.path.split('/').length

                this.transitionName = _to < _from ? 'slide-up' : 'slide-down'
                if (!this.transitionName) {
                    this.transitionName = 'slide-up'
                }
            }
        },
        data() {
            return {
                menu: false,
                alive: true,
                init_finished: false,
                transitionName: null
            }
        },
        async mounted() {
            this.init_finished = await this.$refs.initREF.init()
            console.log('HOME INIT FINISHED =', this.init_finished)
            this.reload()

            if (this.init_finished && this.menu) {
                this.$refs.menu.init()
            }
        },
        methods: {
            reload() {
                this.alive = false
                this.$nextTick(function() { this.alive = true })
            }
        },  
    }
</script>

<style lang="sass" scoped>
.slide-up, .slide-down
    transition: all 1s

.slide-up
    transform: translateY(20vh)
    
.slide-down
    transform: translateY(-20vh)

</style>


