<template>
        <v-navigation-drawer
            v-model="drawer"
            width="290"
            app

            :mini-variant.sync="mini"
            mini-variant-width="68"
            
            enable-resize-watcher
            floating
            permanent
            hide-overlay
            class="right-shadow qiong-pt-20"
        >
        
            <!-- 用户组建 -->
            <menu-msg-widget :id="this.$store.state.user_backend.unid" :need_avatar="false"></menu-msg-widget>

            <!-- 按键列表 -->
            <lad-lister class="qiong-pt-15 mt-2"></lad-lister>

            <v-spacer></v-spacer>

        </v-navigation-drawer>
</template>

<script>
import QiongGradientLine from '../Qiong/Ui/QiongGradientLine.vue'
import LevelImageViewing from '../Viewing/LevelImageViewing.vue'
import LadBonus from './Inner/LadBonus.vue'
import LadLister from './Inner/LadLister.vue'
import LadMsg from './Inner/LadMsg.vue'
import MenuMsgWidget from './MenuMsgWidget.vue'
    export default {
        name: '',
        inject: [ 'reload' ],
        components: {

            LadLister,
                QiongGradientLine,
                LadMsg,
                LadBonus,
                LevelImageViewing,
                MenuMsgWidget
        },
        data() {
            return {
                drawer: false,
                group2: null,
                mini: false,

                mine: null
            }
        },
        watch: {
            group() {
                this.drawer = false
            },
        },
        mounted() {
            this.me()

        },
        methods: {
            changeF() {
                this.drawer = !this.drawer
            },
            init() {
                this.me()
            },
            me(sort = true) {
                const _this = this

                const id = this.$store.state.user_backend.unid
                const many = this.$store.state.users_sponser
                const transfer = this.$store.state.transfer

                if (many) {

                    this.bonus._build_res(many, transfer, function (u, t) {

                        if (u.unid == id) {

                            _this.mine = JSON.parse(JSON.stringify(u))
                        }
                    })
                }
            },

            userCenter() {
                
                this.$router.push({
                    path: '/home/user/center' })
            }
        }
    }
</script>

<style lang="sass" scoped>
    .right-shadow
        // box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.14)

    .last-nav
        position: absolute
        bottom: 0
        width: 100%
</style>


